import React from 'react';
import PropTypes from 'prop-types';
import classNames from 'classnames';
import styled from 'styled-components';


const Wrapper = styled.div`
    position: relative;
    width: 100vw;
    min-height: ${props => props.isBig ? '100vh' : '65vh'};

    &:before {
        content: "";
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: url(${props => props.bgImage});
        background-attachment: fixed;
        background-repeat: no-repeat;
        background-size: cover;
        background-position: center center;
        filter: brightness(60%);
    }
`


const Hero = ({ isBig, img, title, subtitle, children }) => {

    return (
        <>
            <header>
                <Wrapper bgImage={img} isBig={isBig} className={classNames("header-wrapper")}>

                    <div className="content">
                        <h1>{title}</h1>
                        <hr className="my-4 white" />
                        {subtitle && (<p className="text-heading">{subtitle}</p>)}

                        <div>
                            {children}
                        </div>
                    </div>
                </Wrapper>
            </header>
        </>
    )
}

Hero.defaultProps = {
    isBig: false,
    img: "",
    title: "",
    subtitle: "",
}

Hero.propTypes = {
    isBig: PropTypes.bool,
    img: PropTypes.string.isRequired,
    alt: PropTypes.string.isRequired,
    title: PropTypes.string.isRequired,
    subtitle: PropTypes.string.isRequired,
}

export default Hero;