import React, { useState } from 'react';
import { Select, MenuItem } from '@material-ui/core';
import { useTranslation } from 'react-i18next';

import bgFlag from '../assets/images/bg.svg';
import enFlag from '../assets/images/en.svg';


const LanguageMenu = () => {

    const { i18n } = useTranslation();

    const [values, setValues] = useState({
        language: 'bg'
    });

    const handleChange = (event) => {
        i18n.changeLanguage(event.target.value);

        setValues(oldValues => ({
            ...oldValues,
            [event.target.name]: event.target.value
        }));
    }

    return (
        <>

            <Select
                value={values.language}
                onChange={handleChange}
                disableUnderline
                inputProps={{
                    name: 'language'
                }}
            >
                <MenuItem value='bg'><img src={bgFlag} alt="BG" className="nav-flags" /></MenuItem>
                <MenuItem value='en'><img src={enFlag} alt="EN" className="nav-flags" /></MenuItem>
            </Select>
        </>
    )
}

export default LanguageMenu;